// -------------------variables------------------
$accent-color: #3E9CDC;
$menu-hover-color: #43ABF0;
// -------------------fonts------------------

@font-face {
  font-family: 'Lato';
  src: url('../fonts/Lato-Regular.woff2') format('woff2'),
    url('../fonts/Lato-Regular.woff') format('woff'),
    url('../fonts/Lato-Regular.ttf') format('truetype'),
    url('../fonts/Lato-Regular.eot?#iefix') format('embedded-opentype');
  font-weight: 400;
  font-style: normal;
  display: swap;
}

@font-face {
  font-family: 'Lato';
  src: url('../fonts/Lato-Semibold.woff2') format('woff2'),
    url('../fonts/Lato-Semibold.woff') format('woff'),
    url('../fonts/Lato-Semibold.ttf') format('truetype'),
    url('../fonts/Lato-Semibold.eot?#iefix') format('embedded-opentype');
  font-weight: 600;
  font-style: normal;
  display: swap;
}

@font-face {
  font-family: 'Lato';
  src: url('../fonts/Lato-Bold.woff2') format('woff2'),
    url('../fonts/Lato-Bold.woff') format('woff'),
    url('../fonts/Lato-Bold.ttf') format('truetype'),
    url('../fonts/Lato-Bold.eot?#iefix') format('embedded-opentype');
  font-weight: 700;
  font-style: normal;
  display: swap;
}

@font-face {
  font-family: 'Lato';
  src: url('../fonts/Lato-Heavy.woff2') format('woff2'),
    url('../fonts/Lato-Heavy.woff') format('woff'),
    url('../fonts/Lato-Heavy.ttf') format('truetype'),
    url('../fonts/Lato-Heavy.eot?#iefix') format('embedded-opentype');
  font-weight: 900;
  font-style: normal;
  display: swap;
}

// -------------------reset-styles------------------

* {
  margin: 0;
  padding: 0;
  border: 0;
  box-sizing: border-box;
}

a {
  text-decoration: none;
  color: inherit;
}

ul,
ol,
li {
  list-style: none;
}

li {
  position: relative;
}

img {
  vertical-align: top;
  width: 100%;
  height: 100%;
}

button {
  cursor: pointer;
  background-color: unset;
  color: inherit;
  font-family: inherit;
  font-size: inherit;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  margin: 0;
}

:focus {
  outline: none;
}


/* ! ------------------------body-------------------- */

body {
  font-family: 'Lato', 'Arial', 'Helvetica', sans-serif;
  color: #000;
  min-width: 320px;
  font-weight: 400;
  font-style: normal;
  overflow-x: hidden;
}

body.lock,
html.lock {
  overflow: hidden;
}

/* ! ------------------------styles------------------ */

.visually-hidden {
  position: absolute;
  overflow: hidden;
  width: 1px;
  height: 1px;
  clip: rect(1px, 1px, 1px, 1px);
}

.wrapper {
  width: 100%;
  max-width: 1200px;
  margin: 0 auto;
  padding: 0 15px;
}

.header {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  display: flex;
  align-items: center;
  justify-content: center;
  background-color: rgba($color: #10101D, $alpha: 0.9);
  padding: 15px 0;
  min-height: 100px;

  &__wrapper {
    display: flex;
    justify-content: space-between;
    align-items: center;
  }

  &__logo {
    margin-right: 30px;
    max-width: 250px;
    height: 35px;

    a {
      width: 100%;
      height: 100%;
    }

    img {
      object-fit: contain;
      object-position: center;
    }
  }

  &__menu {}
}

.menu {
  display: flex;
  align-items: center;
  flex-wrap: wrap;
  color: #fff;

  &__item {
    margin-right: 60px;
    text-transform: uppercase;
    line-height: 1.2;
    font-size: 13px;
    font-weight: 600;

    &:last-child {
      margin-right: 0;
    }
  }

  &__link {
    display: block;
    padding: 10px 0;
    transition: .3s;
    &:hover {
      color: $menu-hover-color;
    }
  }
}